@charset "UTF-8";
/* reset */
html,
body,
div,
h1,
h2,
h3,
h4,
h5,
h6,
p,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
input,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
textarea,
article,
aside,
audio,
canvas,
figure,
footer,
header,
mark,
menu,
nav,
section,
time,
video {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
body {
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", "Helvetica", Arial, sans-serif;
  background-color: #fff;
  color: #333;
}
article,
aside,
dialog,
figure,
footer,
header,
hgroup,
nav,
section,
blockquote {
  display: block;
}
ul,
ol {
  list-style: none;
}
img {
  border: 0 none;
  vertical-align: top;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
strong,
em,
i {
  font-style: normal;
  font-weight: normal;
}
ins {
  text-decoration: underline;
}
del {
  text-decoration: line-through;
}
mark {
  background: none;
}
input::-ms-clear {
  display: none !important;
}
input,
select,
textarea,
button {
  vertical-align: middle;
  outline: none;
  border: 0;
}
input {
  border-radius: 0;
  box-shadow: none;
}
input[type="button"],
input[type="submit"],
input[type="reset"],
button {
  -webkit-appearance: none;
}
textarea {
  -webkit-appearance: none;
  overflow-y: auto;
  overflow-x: hidden;
  resize: none;
}
/* 如果需要去除掉select元素的默认样式，放开下面的注释即可 */
select {
  appearance: none;
  -ms-appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}
select::-ms-expand {
  display: none;
}
a:link,
a:visited {
  text-decoration: none;
  color: #333;
}
a:hover,
a:active {
  text-decoration: none;
}
/*本页样式*/
.RedAndBlockList {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.RedAndBlockList .topNav {
  padding-right: 10px;
}
.RedAndBlockList .topNav ul {
  overflow: hidden;
  margin-bottom: 10px;
  margin-top: 10px;
}
.RedAndBlockList .topNav ul li {
  font-size: 10px;
  float: right;
  width: 70px;
  height: 25px;
  text-align: center;
  line-height: 25px;
  border-radius: 40px;
  margin-left: 5px;
  cursor: pointer;
}
.RedAndBlockList .conentBox {
  flex: 1;
  overflow: hidden;
  position: relative;
  padding: 0px 10px;
}
.RedAndBlockList .conentBox ul li {
  font-size: 12px;
}
.RedAndBlockList .conentBox ul li .companyName {
  color: #05deff;
  line-height: 20px;
}
.RedAndBlockList .conentBox ul li .jieshao {
  color: white;
  line-height: 20px;
}
.RedAndBlockList .conentBox ul li .time {
  color: white;
  line-height: 20px;
}
.RedAndBlockList .conentBox ul li .incentive {
  color: #333;
  line-height: 20px;
}
.RedAndBlockList .conentBox .showdow {
  position: absolute;
  z-index: 10;
  left: 0px;
  right: 0px;
  bottom: 0px;
  top: 0px;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 20%, rgba(17, 17, 17, 0.4) 90%);
  opacity: 0.9;
}
.RedAndBlockList .activeBtn {
  border: 1px solid #05deff !important;
  color: #05deff !important;
}
.RedAndBlockList .defaultBtn {
  border: 1px solid #42949a !important;
  color: #42949a !important;
}
