.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: right;
  position: relative;
}
.container .item,
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0.9) 0%, rgba(17, 17, 17, 0.66) 52%, rgba(17, 17, 17, 0) 100%);
  padding: 5px 10px;
  height: 33.333%;
}
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.66) 48%, rgba(17, 17, 17, 0.9) 100%);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.average {
  display: flex;
  justify-content: space-around;
  height: 100%;
  padding-top: 10px;
}
.average .left {
  width: 26%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.average .left .treeImg {
  background-image: url(~img/manager/tree.png);
  background-position: center center;
  height: 60px;
  background-repeat: no-repeat;
}
.average .left .treeText {
  color: #fff;
}
.average .left .treeNum,
.average .left .treeUnit {
  font-size: 30px;
  font-weight: bold;
  font-family: fantasy;
  color: #05DEFF;
}
.average .left .treeUnit {
  font-size: 20px;
}
.average .right {
  width: 26%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.average .right .circleBig {
  height: 30%;
  padding: 5px 20px;
  box-shadow: rgba(21, 208, 246, 0.5) 0px 0px 10px 1px inset;
  border-radius: 50%;
  width: 100%;
  top: -10%;
  position: relative;
}
.average .right .circleBig .circleMid {
  padding: 5px 20px;
  height: 80%;
  border-radius: 80%;
  position: relative;
  box-shadow: rgba(21, 208, 246, 0.5) 0px 0px 7px 1px inset;
}
.average .right .circleBig .circleMid .circleSmall {
  top: 5px;
  height: 50%;
  border-radius: 60%;
  position: relative;
  box-shadow: rgba(21, 208, 246, 0.5) 0px 0px 5px 1px inset;
}
.sixContainer {
  height: 100%;
}
.sixContainer .six {
  display: flex;
  height: calc(100% - 50px);
  flex-direction: column;
  justify-content: space-around;
}
.sixContainer .six .container {
  display: flex;
  line-height: 33px;
  font-weight: 700;
  padding: 10px 0;
}
.sixContainer .six .container .imgContainer {
  flex: 1 0 138px;
}
.sixContainer .six .container .imgContainer img {
  margin-right: 5px;
}
.sixContainer .six .container .title {
  color: #ffffff;
  padding: 0 10px;
  flex: 1 0 76px;
}
.sixContainer .six .container .rate {
  color: #40FDFB;
  flex: 1 0 30px;
}
.threeWrapper {
  height: 50%;
  text-align: center;
  padding: 10px 0;
  margin-top: 10px;
  display: flex;
  justify-content: space-around;
}
.threeWrapper .itemImg {
  width: 80%;
}
.threeWrapper .itemImg div:first-child {
  width: 74px;
  height: 4px;
  margin: 0 auto;
}
.threeWrapper .itemImg .iconLine {
  background: linear-gradient(90deg, rgba(34, 136, 255, 0) 0%, rgba(34, 136, 255, 0.99) 50%, rgba(34, 136, 255, 0) 100%);
}
.threeWrapper .itemImg .iconLine2 {
