.sevenContent {
  height: calc(100% - 40px);
}
.sevenContent .sevenWrapper {
  padding: 20px 0px 0px;
  display: flex;
  justify-content: space-around;
  height: 100%;
  text-align: center;
}
.sevenContent .sevenWrapper .itemImg {
  width: 25%;
}
.sevenContent .sevenWrapper .itemImg div:first-child {
  font-size: 35px;
  font-family: Source Han Sans SC;
  font-weight: bold;
}
.sevenContent .sevenWrapper .itemImg .color1 {
