}
.sevenContent .sevenWrapper .itemImg .title {
  margin: 15px 0px;
  font-size: 9px;
  font-family: Source Han Sans SC;
  font-weight: 400;
  color: white;
  opacity: 0.7;
}
.sevenContent .sevenWrapper .itemImg .day,
.sevenContent .sevenWrapper .itemImg .week {
