.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: right;
  position: relative;
}
.container .item,
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0.9) 0%, rgba(17, 17, 17, 0.66) 52%, rgba(17, 17, 17, 0) 100%);
  padding: 5px 10px;
  height: 33.333%;
}
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.66) 48%, rgba(17, 17, 17, 0.9) 100%);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.icon {
  background-image: url("/img/Group.png");
  background-repeat: no-repeat;
  width: 23px;
  height: 10px;
  margin-right: 6px;
  margin-left: 6px;
}
.headerWrapper {
  display: flex;
  line-height: 48px;
}
.headerWrapper .titleWrapper {
  flex: 1 0 200px;
  border-bottom: 1px solid;
  margin-right: 10px;
  border-image: linear-gradient(to left, #6cf5b0, #6dfafe, #2ba6ff) 1;
}
.headerWrapper .titleWrapper > div {
  display: inline-block;
}
.headerWrapper .titleWrapper .title {
  color: #fff;
}
.headerWrapper .rightIconWrapper {
  border-bottom: 1px solid #CACCD2;
  text-align: center;
  width: 25px;
}
.headerWrapper .rightIconWrapper .rightIcon {
  display: inline-block;
  background-image: url("/img/Shape.png");
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
}
