.serviceContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: calc(100% - 50px);
}
.serviceContainer .serviceWrapper {
  display: flex;
  font-size: 12px;
}
.serviceContainer .serviceWrapper .before {
  flex: 1 0 120px;
  color: #ffffff;
  margin-right: 2px;
}
.serviceContainer .serviceWrapper .before .number {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  background-color: #05D9F9;
  color: #000000;
  font-weight: 700;
  margin-right: 10px;
}
.serviceContainer .serviceWrapper .progress {
  margin-top: -4px;
  margin-right: 5px;
}
.serviceContainer .serviceWrapper .progress .ant-progress-inner {
  background-color: rgba(0, 0, 0, 0);
}
.serviceContainer .serviceWrapper .after {
  flex: 1 0 50px;
  color: #05DEFF;
}
.serviceContainer .serviceWrapper .after .img {
  width: 12px;
  margin-left: 5px;
}
.twelveBox {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.twelveBox .infoItem {
  width: 40%;
  margin-top: -15%;
  text-align: center;
}
.twelveBox .infoItem .itemPic {
  height: 100%;
}
.twelveBox .infoItem .itemNumber {
  margin-top: 12%;
  font-size: 36px;
  font-family: Impact;
  font-weight: 400;
  color: #05deff;
}
.twelveBox .infoItem .itemName {
  margin-top: 12%;
  font-size: 14px;
  font-family: 'Microsoft YaHei';
  font-weight: 400;
  color: #fefefe;
}
.serviceNumber {
  margin-top: 25px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.serviceNumber .content {
  height: 70%;
}
