.EightPage {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.EightPage .echartsContainer {
  position: relative;
  flex: 1;
  padding-top: 15%;
  box-sizing: border-box;
}
.EightPage .echartsBox {
  width: 60%;
  height: 90%;
  box-sizing: border-box;
  position: absolute;
  left: 0px;
  top: 15%;
  right: 0px;
  bottom: 0px;
  margin: auto;
}
.EightPage .echartsBox .imgICon {
  position: absolute;
  width: 30px;
  height: 30px;
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  margin: auto;
}
.EightPage .echartsBox .imgICon img {
  width: 100%;
  height: 100%;
}
.EightPage .echartsBox .titlePannel {
  position: absolute;
  right: -13%;
  top: 10%;
}
.EightPage .echartsBox .titlePannel .titleName {
  color: #4de4fe;
  font-size: 12px;
}
.EightPage .echartsBox .titlePannel .Namevalue {
  margin-top: 5px;
  display: inline-block;
  padding: 0px 5px;
  color: #fff;
  font-size: 18px;
  border-bottom: 1px solid #fff;
  padding-bottom: 3px;
}
