.fiveMain {
  width: 100%;
  height: 100%;
}
.fiveMain .infoItemBox {
  width: calc(100% - 25px);
  height: 25%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.fiveMain .infoItemBox .infoItem {
  width: 25%;
  text-align: center;
}
.fiveMain .infoItemBox .infoItem .itemNumber {
  font-size: 8px;
  font-family: Source Han Sans SC;
  font-weight: 400;
  color: #40fdfb;
}
.fiveMain .infoItemBox .infoItem .itemName {
  font-size: 6px;
  font-family: Source Han Sans SC;
  font-weight: 400;
  color: white;
}
.fiveMain .chart {
  width: calc(100% - 25px);
  height: 53%;
}
