.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: right;
  position: relative;
}
.container .item,
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0.9) 0%, rgba(17, 17, 17, 0.66) 52%, rgba(17, 17, 17, 0) 100%);
  padding: 5px 10px;
  height: 33.333%;
}
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.66) 48%, rgba(17, 17, 17, 0.9) 100%);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.fourContainer {
  height: 100%;
}
.fourContainer .itemBox {
  padding: 13px 25px 13px 19px;
  height: calc(100% - 49px);
  display: flex;
  flex-direction: column;
}
.fourContainer .itemBox .progressInfo {
  font-size: 10px;
  font-weight: 300;
  opacity: 0.7;
  flex: 1;
  display: flex;
  align-items: center;
}
.fourContainer .itemBox .progressInfo .name {
  color: #fefefe;
}
.fourContainer .itemBox .progressInfo .count {
  color: #3ED5DE;
  text-align: right;
}
