/**
 * @currentAffairsWrap:当日办事渠道排行模块父级样式设置
 */
.currentAffairsWrapFourChart {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1000;
}
.currentAffairsWrapFourChart .headerFourChart {
  width: 100%;
}
.currentAffairsWrapFourChart .contentFourChart {
  margin-top: 15px;
}
.currentAffairsWrapFourChart .contentFourChart .infoShowWrapFourChart {
  width: 90%;
  margin-left: 25px;
}
.currentAffairsWrapFourChart .contentFourChart .infoShowWrapFourChart .infoShowFourChart {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 24px;
}
.currentAffairsWrapFourChart .contentFourChart .infoShowWrapFourChart .infoShowFourChart .progressFourChart {
  width: 300px;
  margin: 0;
  margin-left: 20px;
  display: flex;
  justify-content: start;
  align-items: center;
}
.currentAffairsWrapFourChart .contentFourChart .infoShowWrapFourChart .infoShowFourChart .progressFourChart span {
  color: #05DEFF;
  font-size: 14px;
  margin-left: 12px;
  line-height: 14px;
  display: inline-block;
  width: 50px;
  text-align: right;
}
.currentAffairsWrapFourChart .contentFourChart .infoShowWrapFourChart .infoShowFourChart .progressFourChart img {
  width: 10px;
  height: 13px;
  margin-left: 4px;
}
.currentAffairsWrapFourChart .contentFourChart .infoShowWrapFourChart .infoShowFourChart .infoTxtFourChart {
  width: 100px;
  font-size: 14px;
  display: flex;
  justify-content: start;
  align-items: center;
}
.currentAffairsWrapFourChart .contentFourChart .infoShowWrapFourChart .infoShowFourChart .infoTxtFourChart .rankingFourChart {
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 100%;
  background: #05DEFF;
  font-size: 10px;
  color: #041B38;
  line-height: 12px;
  text-align: left;
  text-indent: 2.5px;
}
.currentAffairsWrapFourChart .contentFourChart .infoShowWrapFourChart .infoShowFourChart .infoTxtFourChart .ditchFourChart {
  margin-left: 13px;
  font-size: 13px;
  color: #FFFFFF;
}
.currentAffairsWrapFourChart .ant-progress-inner {
  border-radius: 0 !important;
  background: rgba(0, 0, 0, 0.5);
  position: relative;
  top: -2px;
}
.currentAffairsWrapFourChart .ant-progress-success-bg,
.currentAffairsWrapFourChart .ant-progress-bg {
  border-radius: 0 !important;
}
