/**
 * @currentAffairsWrap:当日办事渠道排行模块父级样式设置
 */
.currentAffairsWrapFour {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1000;
}
.currentAffairsWrapFour .headerFour {
  width: 100%;
}
.currentAffairsWrapFour .contentFour {
  margin-top: 15px;
}
.currentAffairsWrapFour .contentFour .contentHeaderFour {
  color: #4B8CD3;
  font-size: 12px;
}
.currentAffairsWrapFour .contentFour .contentHeaderFour span:nth-child(1) {
  margin-left: 23px;
}
.currentAffairsWrapFour .contentFour .contentHeaderFour span:nth-child(2) {
  margin-left: 10px;
}
.currentAffairsWrapFour .contentFour .infoShowWrapFour {
  width: 90%;
  margin-left: 25px;
  margin-top: 4px;
}
.currentAffairsWrapFour .contentFour .infoShowWrapFour .infoShowFour {
  margin-top: 20px;
}
.currentAffairsWrapFour .contentFour .infoShowWrapFour .infoShowFour .infoTxtFour {
  width: 100%;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.currentAffairsWrapFour .contentFour .infoShowWrapFour .infoShowFour .infoTxtFour .rankingDitchWrapFour {
  width: 60%;
  display: flex;
  justify-content: start;
  align-items: center;
}
.currentAffairsWrapFour .contentFour .infoShowWrapFour .infoShowFour .infoTxtFour .rankingDitchWrapFour .rankingFour {
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 100%;
  background: #05DEFF;
  font-size: 10px;
  color: #041B38;
  line-height: 14px;
  text-align: center;
}
.currentAffairsWrapFour .contentFour .infoShowWrapFour .infoShowFour .infoTxtFour .rankingDitchWrapFour .ditchFour {
  margin-left: 4px;
  font-size: 13px;
  color: #FFFFFF;
}
.currentAffairsWrapFour .contentFour .infoShowWrapFour .infoShowFour .infoTxtFour .proportionFour {
  color: #05DEFF;
  font-size: 14px;
}
.currentAffairsWrapFour .ant-progress-inner {
  border-radius: 0 !important;
  background: rgba(0, 0, 0, 0.5);
}
.currentAffairsWrapFour .ant-progress-success-bg,
.currentAffairsWrapFour .ant-progress-bg {
  border-radius: 0 !important;
}
