.FooterPage {
  background: linear-gradient(90deg, #111111 0%, rgba(17, 17, 17, 0.3) 60%, #111111 100%);
  height: 2.60417vw;
  z-index: 2;
  padding: 0;
}
.FooterPage .FooterListBox {
  margin-bottom: 0;
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: center;
}
.FooterPage .FooterListBox a {
  color: #CACCD2;
}
.FooterPage .FooterListBox a:active {
  color: #05deff;
}
.FooterPage .FooterListBox li {
  display: inline-block;
  color: #CACCD2;
  font-size: 0.83333vw;
  height: 100%;
  line-height: 2.60417vw;
}
.FooterPage .FooterListBox li:hover {
  cursor: pointer;
}
.FooterPage .FooterListBox li:last-child .FooterVerLine {
  width: 0;
}
.FooterPage .FooterListBox li .FooterList {
  margin: 0 0.9375vw;
}
.FooterPage .FooterListBox li .FooterVerLine {
  display: inline-block;
  width: 1px;
  height: 1.25vw;
  background-color: #CACCD2;
  vertical-align: text-top;
}
.FooterPage .FooterListBox .FooterActive {
  display: inline-block;
  color: #05deff;
  background: url("/img/footer/activeBg.png") no-repeat;
  background-size: 100% 100%;
  height: 100%;
}
