.currentAffairsWrapOne {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1000;
}
.currentAffairsWrapOne .headerOne {
  width: 100%;
}
.currentAffairsWrapOne .contentOne {
  margin-top: 7px;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne .headerTitleOne {
  width: 100px;
  text-align: center;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne .headerTitleOne h5 {
  font-size: 16px;
  color: #FFFFFF;
  font-weight: normal;
  margin: 0;
  margin-top: 4px;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne .headerTitleOne span {
  font-size: 18px;
  color: #FA6767;
  display: inline-block;
  margin-top: 4px;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne .headerTitleOne .redAnnouncedLegalPerson {
  color: #FA6767;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne .headerTitleOne .blackAnnouncedLegalPerson {
  color: #A290FF;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne .headerTitleOne .blackAnnouncedPerson {
  color: #A290FF;
}
.currentAffairsWrapOne .contentOne .tipTitleOne {
  color: #FFFFFF;
  font-size: 6px;
  text-align: right;
  margin-top: 5px;
}
.currentAffairsWrapOne .contentOne .tipTitleOne span {
  display: inline-block;
  width: 15px;
  height: 6px;
  border-radius: 3px;
}
.currentAffairsWrapOne .contentOne .tipTitleOne .redAnnouncementOne {
  background: #F7517F;
  margin-right: 4px;
}
.currentAffairsWrapOne .contentOne .tipTitleOne .blackAnnouncementOne {
  background: #6648FF;
  margin-right: 4px;
}
.currentAffairsWrapOne .contentOne .tipTitleOne .blackTxt {
  margin-right: 32px;
  width: auto;
}
.currentAffairsWrapOne .contentOne .tipTitleOne .redkTxt {
  width: auto;
  margin-right: 8px;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne {
  display: flex;
  justify-content: start;
  align-items: stretch;
  height: 130px;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .leftProgressOne {
  width: 60%;
  height: 100%;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .leftProgressOne .leftDataProgressOne {
  height: 100%;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .leftProgressOne .leftDataProgressOne .leftDataProgressShowOne {
  height: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .leftProgressOne .leftDataProgressOne .leftDataProgressShowOne h5 {
  color: #FFFFFF;
  margin-left: 13px;
  font-size: 7px;
  font-weight: normal;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .leftProgressOne .leftDataProgressOne .leftDataProgressShowOne .progressDataShow {
  width: 0;
  background: #F7517F;
  height: 8px;
  border-radius: 4px 0 0 4px;
  position: relative;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .leftProgressOne .leftDataProgressOne .leftDataProgressShowOne .progressDataShow span {
  text-align: center;
  width: 15px;
  height: 15px;
  line-height: 12px;
  color: #FFFFFF;
  font-size: 6px;
  position: absolute;
  left: -15px;
  top: 0px;
  bottom: 0;
  margin: auto;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .middleLineOne {
  width: 2px;
  height: 120px;
  background: #756E9F;
  position: relative;
  top: -1px;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .rightProgressOne {
  width: 30%;
  display: flex;
  height: 100%;
  justify-content: space-around;
  flex-direction: column;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .rightProgressOne .rightDataProgressOne {
  height: 100%;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .rightProgressOne .rightDataProgressOne .leftDataProgressShowOne {
  height: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .rightProgressOne .rightDataProgressOne .leftDataProgressShowOne .progressDataShowRight {
  width: 0;
  background: #6648FF;
  height: 8px;
  border-radius: 0px 4px 4px 0;
  position: relative;
}
.currentAffairsWrapOne .contentOne .dataViewShowOne .dataViewOne .rightProgressOne .rightDataProgressOne .leftDataProgressShowOne .progressDataShowRight span {
  text-align: center;
  width: 15px;
  height: 15px;
  line-height: 12px;
  color: #FFFFFF;
  font-size: 6px;
  position: absolute;
  right: -15px;
  top: 0px;
  bottom: 0;
  margin: auto;
}
.currentAffairsWrapOne .dataViewOne .ant-progress-inner {
  border-radius: 0 8px 8px 0 !important;
  background: rgba(0, 0, 0, 0);
}
.currentAffairsWrapOne .dataViewOne .ant-progress-success-bg,
.currentAffairsWrapOne .ant-progress-bg {
  border-radius: 0 8px 8px 0 !important;
}
.currentAffairsWrapOne .dataViewOne .ant-progress-line {
  font-size: 0;
}
