.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: right;
  position: relative;
}
.container .item,
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0.9) 0%, rgba(17, 17, 17, 0.66) 52%, rgba(17, 17, 17, 0) 100%);
  padding: 5px 10px;
  height: 33.333%;
}
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.66) 48%, rgba(17, 17, 17, 0.9) 100%);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.six {
  height: 100%;
}
.six .bg {
  text-align: center;
  margin-top: 10px;
}
.six .marquee {
  color: #ffffff;
  font-size: 12px;
  line-height: 20px;
  opacity: 0.6;
  padding: 10px 20px;
}
.six .marquee div {
  margin: 0 auto;
  text-align: center;
  width: 90%;
}
