@charset "UTF-8";
.HeaderPage {
  width: 100%;
  height: 100px;
  background: linear-gradient(180deg, rgba(17, 17, 17, 0.92) 0%, rgba(17, 17, 17, 0) 100%);
  opacity: 0.8;
  z-index: 99;
}
.HeaderPage  .leftBox {
  float: left;
  height: 100%;
  width: 400px;
}
.HeaderPage  .leftBox .logoBox {
  width: 755px;
  height: 49px;
  position: absolute;
  left: 40px;
  top: 31px;
  background: white;
  opacity: 1;
  background: url("~img/header/logoIcon.png");
}
.HeaderPage .rightBox {
  float: right;
  position: relative;
  height: 100%;
}
.HeaderPage .rightBox .weatherpicBox {
  float: left;
  position: absolute;
  right: 154px;
  top: 15px;
  width: 90px;
  height: 90px;
  background: rgba(0, 0, 0, 0);
  opacity: 1;
  background: url("~img/header/weatherIcon.png");
}
.HeaderPage .rightBox .dateBox {
  position: absolute;
  right: 268px;
  top: 65px;
  width: 148px;
  height: 16px;
  font-size: 16px;
  font-family: NotoSansHans-Regular;
  line-height: 21px;
  color: #d9d9d9;
  opacity: 1;
}
.HeaderPage .rightBox .weekBox {
  position: absolute;
  right: 268px;
  top: 25px;
  width: 123px;
  height: 40px;
  font-size: 28px;
  font-weight: bold;
  font-family: NotoSansHans-Bold;
  line-height: 40px;
  color: #d9d9d9;
  opacity: 1;
}
.HeaderPage .rightBox .temperatureBox {
  position: absolute;
  right: 60px;
  top: 25px;
  width: 70px;
  height: 40px;
  font-size: 28px;
  font-weight: bold;
  font-family: NotoSansHans-Bold;
  line-height: 40px;
  color: #d9d9d9;
  opacity: 1;
}
.HeaderPage .rightBox .weatherBox {
  position: absolute;
  right: 60px;
  top: 65px;
  width: 64px;
  height: 16px;
  font-size: 16px;
  font-family: NotoSansHans-Regular;
  line-height: 21px;
  color: #d9d9d9;
  opacity: 1;
}
.HeaderPage .headLine {
  position: relative;
  width: 100%;
  height: 1px;
  top: 100px;
  background: linear-gradient(270deg, #6cf5b0 0%, #6dfafe 44%, #2ba6ff 94%, rgba(24, 143, 255, 0) 100%);
}
