.container {
  height: 100%;
  overflow: hidden;
}
.container .leftWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: left;
  position: relative;
}
.container .rightWrapper {
  z-index: 2;
  height: 100%;
  width: 23%;
  float: right;
  position: relative;
}
.container .item,
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0.9) 0%, rgba(17, 17, 17, 0.66) 52%, rgba(17, 17, 17, 0) 100%);
  padding: 5px 10px;
  height: 33.333%;
}
.container .itemRight {
  background: linear-gradient(90deg, rgba(17, 17, 17, 0) 0%, rgba(17, 17, 17, 0.66) 48%, rgba(17, 17, 17, 0.9) 100%);
}
.container .noBottonPad {
  padding-bottom: 0px;
}
.content {
  height: calc(100% - 68px);
}
.radioButton {
  background: #65B3FA !important;
  border-radius: 4px;
  outline: none;
  color: #fff;
  border-color: #65B3FA;
  border: 0;
}
.radioButton:hover {
  background-color: #fff !important;
  color: #313131;
  border-color: #313131;
  border: 0;
}
.active {
  background-color: #fff !important;
  color: #313131;
  border-color: #313131;
  border: 0;
  display: inline-block;
}
