.currentAffairsWrapNineChart {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1000;
}
.currentAffairsWrapNineChart .headerNineChart {
  width: 100%;
}
.currentAffairsWrapNineChart .topNineChart {
  padding-right: 10px;
}
.currentAffairsWrapNineChart .topNineChart ul {
  overflow: hidden;
  margin-bottom: 10px;
  margin-top: 15px;
}
.currentAffairsWrapNineChart .topNineChart ul li {
  font-size: 10px;
  float: right;
  width: 70px;
  height: 25px;
  text-align: center;
  line-height: 25px;
  border-radius: 40px;
  margin-left: 5px;
  cursor: pointer;
}
.currentAffairsWrapNineChart .contentNineChart {
  margin-top: 30px;
}
.currentAffairsWrapNineChart .contentNineChart .infoShowWrapNineChart {
  width: 90%;
  margin-left: 25px;
}
.currentAffairsWrapNineChart .contentNineChart .infoShowWrapNineChart .infoShowNineChart {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 17px;
}
.currentAffairsWrapNineChart .contentNineChart .infoShowWrapNineChart .infoShowNineChart .progressNineChart {
  width: 85%;
  margin: 0;
  margin-left: 4px;
  display: flex;
  justify-content: start;
  align-items: center;
}
.currentAffairsWrapNineChart .contentNineChart .infoShowWrapNineChart .infoShowNineChart .progressNineChart span {
  color: #05DEFF;
  font-size: 14px;
  margin-left: 12px;
  line-height: 14px;
}
.currentAffairsWrapNineChart .contentNineChart .infoShowWrapNineChart .infoShowNineChart .infoTxtNineChart {
  width: 100px;
  font-size: 14px;
  display: flex;
  justify-content: start;
  align-items: center;
}
.currentAffairsWrapNineChart .contentNineChart .infoShowWrapNineChart .infoShowNineChart .infoTxtNineChart .rankingNineChart {
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 100%;
  background: #05DEFF;
  font-size: 10px;
  color: #041B38;
  line-height: 14px;
  text-align: center;
}
.currentAffairsWrapNineChart .contentNineChart .infoShowWrapNineChart .infoShowNineChart .infoTxtNineChart .ditchNineChart {
  margin-left: 13px;
  font-size: 13px;
  color: #FFFFFF;
}
.currentAffairsWrapNineChart .ant-progress-inner {
  border-radius: 0 !important;
  background: rgba(0, 0, 0, 0.5);
  position: relative;
  top: -2px;
}
.currentAffairsWrapNineChart .ant-progress-success-bg,
.currentAffairsWrapNineChart .ant-progress-bg {
  border-radius: 0 !important;
}
.currentAffairsWrapNineChart .activeBtn {
  border: 1px solid #05deff !important;
  color: #05deff !important;
}
.currentAffairsWrapNineChart .defaultBtn {
  border: 1px solid #42949a !important;
  color: #42949a !important;
}
