.twelveContainer {
  height: 100%;
}
.twelveContainer .infoBox {
  height: calc(98% - 49px);
  position: relative;
  padding: 19px 25px 9px 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.twelveContainer .infoBox .item {
  width: 100%;
  border: 1px solid rgba(71, 224, 230, 0.4);
  display: flex;
}
.twelveContainer .infoBox .item .left {
  flex: 3;
  padding: 12px;
}
.twelveContainer .infoBox .item .left .image {
  border: 1px solid rgba(71, 224, 230, 0.4);
  width: 100%;
  height: 100%;
}
.twelveContainer .infoBox .item .right {
  flex: 4;
  padding: 12px;
}
.twelveContainer .infoBox .item .right .type {
  background: linear-gradient(90deg, rgba(39, 166, 189, 0.98), rgba(69, 204, 254, 0));
  color: #F0F4F5;
  height: 24px;
  margin-bottom: 10px;
  line-height: 24px;
  font-size: 14px;
  padding: 0 4px;
}
.twelveContainer .infoBox .item .right .info {
  color: #B2BDBF;
  line-height: 17px;
  font-size: 12px;
  padding: 0 4px;
}
