.twoBox {
  width: 100%;
  height: 100%;
}
.twoBox .title {
  padding: 12px 26px 0px;
  width: 100%;
  opacity: 1;
  font-size: 14px;
  font-family: Microsoft YaHei;
  line-height: 16px;
}
.twoBox .title .title1 {
  width: 85px;
  height: 15px;
  font-weight: 400;
  color: white;
}
.twoBox .title .title2 {
  width: 39px;
  height: 12px;
  font-weight: bold;
  color: #40fdfb;
}
.twoBox .title3 {
  padding: 10px 26px 0px;
  width: 100%;
  opacity: 1;
  font-size: 14px;
  font-family: Microsoft YaHei;
  line-height: 16px;
}
.twoBox .title3 .title4 {
  width: 85px;
  height: 15px;
  font-weight: 400;
  color: white;
}
.twoBox .title3 .title5 {
  width: 39px;
  height: 12px;
  font-weight: bold;
  color: #40fdfb;
}
.twoBox .chart {
  width: calc(95% - 15px);
  height: 88%;
}
